 <template>
 	<view class="zai-box" id="login-page">
 		<image src="@/static/qa/App-bg.jpg" alt="image" class="login-bg-img" />
 		<image src="@/static/qa/LOGO.png" alt="image" style="width:159px;height:80px" />
 		<scroll-view scroll-y class="page" style="background-color: rgba(0, 0, 0, 0);">
 			<view class="box padding-lr-xl login-paddingtop" style="padding-top:10%">
 				<view style="display: flex;justify-content: center;align-items: center;">
 					<view class="zai-title">
 						<text
 							style="text-align:center; font-size: 20px;color: black;display: block;font-weight: bold;">QA Platform</text>
 						<text style="text-align:center;font-size: 20px;color:black;display: flex;align-items: center;justify-content: center;font-weight: bold;">Test Case Management</text>
 					</view>
 				</view>
 				<view>
 					<text
 						style="padding-top: 25px;font-size: 15px;color: #ffffff;display: block;">User</text>
 					<view class="login-input cu-form-group shadow-blur">
 						<uni-easyinput @blur="queryPhone" style="display: block; padding: 5px;" v-model="userName"
 							focus placeholder="Please enter the account"></uni-easyinput>
 					</view>
 					<text style=" font-size: 15px;color: #ffffff;display: block;">Password</text>
 					<view class="login-input cu-form-group" style="position: relative;">
 						<view class="eyeBtn" :class="showPswd? 'eyeClass':''" @click="eyeChange()"></view>
 						<uni-easyinput style="padding: 5px;" v-show="!showPswd" v-model="password" focus
 							type="password" placeholder="Please enter the password"></uni-easyinput>
 						<uni-easyinput v-show="showPswd" v-model="password" focus type="text"
 							placeholder="Please enter the password"></uni-easyinput>
 					</view>
 					<text style="font-size: 15px;color: #ffffff;display: block;">Phone Number</text>
 					<view class="login-input cu-form-group shadow-blur">
 						<u-input style="height: 75%;border: 0;" disabled
 							placeholder="Please enter the user associated with the mobile number" v-model="phone">
 							<template slot="suffix" style="background-color: #ffffff;">
 								<u-code ref="uCode" @change="codeChange" seconds="120" startText="Send Code"
 									changeText="Xs Regain"></u-code>
 								<u-button style="height: 4vh;background-color: #666464;border: 0;" @tap="getCode"
 									:text="tips" type="success" size="mini"></u-button>
 							</template>
 						</u-input>
 					</view>

 					<text style="font-size: 15px;color: #ffffff;display: block;">Verification Code</text>
 					<view class="login-input cu-form-group shadow-blur">
 						<uni-easyinput maxlength="6" style="display: block; padding: 5px;" v-model="code" focus
 							placeholder="Please enter the Verification Code"></uni-easyinput>
 					</view>
 				</view>
 				<view class="flex  flex-direction">
 					<button class="cu-btn shadow-blur round lg" :loading="loading" @tap="onLogin">
 						{{loading ? "Logging In...":"Login"}}
 					</button>
 				</view>
 				<view class="forgetPassword"><a @click="forget">Forget Password</a></view>
 			</view>
 		</scroll-view>
 		<!-- 登录加载弹窗 -->
 		<view class="cu-load load-modal" v-if="loading">
 			<image src="/static/login3.png" mode="aspectFit"></image>
 		</view>
 		<u-popup :round="10" style="    position: absolute;
				bottom: 10%;
				text-align: center;
				left: 30%;
			}" v-show="showpopup" mode="center">
 			<view class="progressBox">
 				<!-- <u-loading-icon size="36"></u-loading-icon> -->
 				<text class="words">Do Not Exit While Downloading {{percentVal}}%</text>
 			</view>

 		</u-popup>
 	</view>

 </template>

