 		background-color: transparent !important;
 		background-image: none !important;
 		color: -internal-light-dark(black, white) !important;
 	}

 	.login-paddingtop {
 		padding-top: 200 upx;
 	}

 	.zai-box {
 		position: absolute;
 		width: 100%;
 		height: 100%;
 		display: flex;
 		justify-content: center;
 		flex-direction: column;
 		align-items: center;
 	}

 	.zai-logo {
 		width: 60px !important;
 	}

 	.zai-title {
 		font-size: 14px;
 		color: #999999;

 	}

 	.input-placeholder,
 	.zai-input {
 		color: #94afce;
 	}

 	.zai-label {
 		padding: 60 upx 0;
 		text-align: center;
 		font-size: 20 upx;
 		color: #a7b6d0;
 	}

 	.zai-btn {
 		background: #ff65a3;
 		color: #fff;
 		border: 0;
 		border-radius: 100 upx;
 		font-size: 36 upx;
 	}

 	.zai-btn:after {
 		border: 0;
 	}

 	/*按钮点击效果*/
 	.zai-btn.button-hover {
 		transform: translate(1 upx, 1 upx);
 	}

 	.padding-lr-xl {
 		padding-left: 50upx;
 		padding-right: 30upx;
 	}

 	input:-internal-autofill-selected {
 		background-color: transparent !important;
 	}

 	.eyeBtn {
 		width: 22px;
 		height: 22px;
 		position: absolute;
 		top: 15px;
 		right: 10px;
 		z-index: 9;
 		background: url(@/static/pass-hide@3x.png) no-repeat center center;
 		background-size: 20px;
 	}

 	.eyeClass {
 		width: 22px;
 		height: 22px;
 		position: absolute;
 		top: 15px;
 		right: 10px;
 		z-index: 9;
 		background: url(@/static/pass-show@3x.png) no-repeat center center;
 		background-size: 20px;
 	}
